import { Injectable } from '@angular/core';
import { HttpService } from '../../../app/rtf-common';
import { CacheService } from '../../../app/rtf-common';
import { environment } from '../../../environments/environment';

@Injectable()
export class UserinfoService {

  private Api_Language = '/api/runtime/sys/v1.0/login-infos?infoType=supportedLanguage';
  private Api_Tenants = '/api/runtime/sys/v1.0/login-infos?infoType=tenants';
  private Api_UserExtend = '/api/runtime/sys/v1.0/userinfos?infoType=user';
  private Api_UserTenant = '/api/runtime/sys/v1.0/userinfos?infoType=tenant';

  constructor(private http: HttpService, private cache: CacheService) {
    if (!environment.production) {
      this.Api_Language = 'http://localhost:5000/api/runtime/sys/v1.0/login-infos?infoType=supportedLanguage';
      this.Api_Tenants = 'http://localhost:5000/api/runtime/sys/v1.0/login-infos?infoType=tenants';
      this.Api_UserExtend = 'http://localhost:5000/api/runtime/sys/v1.0/userinfos?infoType=user';
      this.Api_UserTenant = 'http://localhost:5000/api/runtime/sys/v1.0/userinfos?infoType=tenant';
    }
  }


  GetSupportedLanguage() {
    return this.http.get(this.Api_Language);
  }

  GetTenants() {
    return this.http.get(this.Api_Tenants);
  }

  GetUserExtendInfo() {
    return this.http.get(this.Api_UserExtend);
  }

  GetCurrentTenantAndLanguage() {
    return this.http.get(this.Api_UserTenant);
  }
}



