import { Component, OnInit } from '@angular/core';
import { UserinfoService } from '../../userinfo.service';
import { Router } from '@angular/router';
import { UserExtend } from '../../models/user.model';
import { CacheService } from '../../../../../app/rtf-common';
import { I18n_frm } from '../../../../models/rtf.i18n.model';

@Component({
  selector: 'app-userinfo',
  templateUrl: './userinfo.component.html',
  styleUrls: ['./userinfo.component.css'],
  host: {
    'class': 'd-flex flex-column g-main gw-userinfo'
  }
})
export class UserinfoComponent implements OnInit {

  // =['','']
  languageItems: any[];
  selectedLanguae: string;
  defaultLanguage = '';
  countryItems: Array<string> = ['', ''];

  tenantItems: any[];
  selectedTenant = '';
  tenantName: string;
  lanName: string;

  opened: boolean;
  uservice: any;
  userExtend: UserExtend;
  tl: any;
  I18n: any;
  tenantChanged: boolean;
  languageChanged: boolean;

  username: string;
  email: string;
  mphone: string;

  // public data: Array<string>
  constructor(private userservice: UserinfoService, private router: Router, private cache: CacheService) {
    // this.data = this.languageItems.slice();
    this.uservice = userservice;
  }

  ngOnInit() {
    this.I18n = I18n_frm;
    if (this.cache.get('languageCode')) {
      this.defaultLanguage = this.cache.get('languageCode');
    }
    this.uservice.GetSupportedLanguage().subscribe(res => {
      this.languageItems = res;
      const arr = [];
      this.languageItems.forEach(item => {
        arr.push(item.name);
      });
      this.languageItems = arr;
    });

    this.uservice.GetTenants().subscribe(res => {
      this.tenantItems = res;
      const arr = [];
      this.tenantItems.forEach(item => {
        arr.push(item.name);
      });
      this.tenantItems = arr;
    });

    this.uservice.GetUserExtendInfo().subscribe(res => {
      // this.cache.set('userinfo', res);
      // this.userExtend = this.cache.get('userinfo');
      this.userExtend = res;
      this.username = this.userExtend.name;
      this.email = this.userExtend.email;
      this.mphone = this.userExtend.mobilePhone;
      console.log(this.userExtend);
    });

    this.userservice.GetCurrentTenantAndLanguage().subscribe(res => {
      this.tl = res;
      this.tenantName = this.tl.tenantName;
      this.lanName = this.tl.lanName;
      this.selectedTenant = this.tenantName;
      this.selectedLanguae = this.lanName;
      console.log(this.tl);
    });
  }

  public TenantValueChange(value: any): void {
    this.tenantChanged = true;
    this.opened = true;
  }

  public LanguageValueChange(value: any): void {
    this.languageChanged = true;
    this.opened = true;
  }


  public close() {
    this.opened = false;
    this.tenantChanged = false;
    this.languageChanged = false;
  }

  public open() {
    this.opened = true;
  }

  public relogin() {
    this.router.navigateByUrl('login');
  }
}



